@import './var.scss';
$max-tab-width: 750px;

$tab-bg-color: #e6eaef;
$active-tab-bg-color: #ffffff;

.pool-tabs {
  .ant-tabs-nav {
    margin-bottom: 0;
  }
  .ant-tabs-nav-wrap {
    margin: auto;
   
    .ant-tabs-nav-list {
      padding: 0;
      margin: 0;
      width: 100%;
      display: flex;
      justify-content: space-around;
      align-content: center;
      max-width: min(728px, 90vw);
      background: $tab-bg-color;
      overflow: hidden;
      border-radius: 15px 15px 0px 0px;

      @include mobile-only {
        width: calc(100% - 30px);
      }

      .ant-tabs-ink-bar {
        display: none;
      }

      .ant-tabs-tab {
        position: relative;
        width: 100%;
        margin: 0;
        display: flex;
        align-content: center;
        justify-content: center;
        background: $tab-bg-color;
        border: 0;
        font-weight: bold;
        font-size: 18px;
        line-height: 22px;
        height: 60px;

      @include mobile-only {
          font-size: 14px;
          line-height: 17px;
          height: 40px;
        }
        .ant-tabs-tab-btn {
          color: #221c34;
        }
      }
      .ant-tabs-tab-active {
        background: $active-tab-bg-color;
        .ant-tabs-tab-btn {
          color: #5915e1;

          > span {
            text-shadow: none !important;
          }
        }
      }

      .ant-tabs-tab-active {
        z-index: 2;
        &::before,
        &::after {
          content: ' ';
          position: absolute;
          top: 0;
          width: 37px;
          height: 60px;
        }
        &::before {
          background-image: url(../images/curved_nav_left.png);
          left: -36px;
        }
        &::after {
          background-image: url(../images/curved_nav_right.png);
          right: -36px;
        }

        @include mobile-only {
          &::before,
          &::after {
            width: 23px;
            height: 40px;
          }
          &::before {
            background-image: url(../images/curved_nav_left_mobile.png);
            left: -22px;
          }
          &::after {
            background-image: url(../images/curved_nav_right_mobile.png);
            right: -22px;
          }
        }
      }
    }
  }

  .ant-tabs-content-holder {
    margin-top: -2rem;
    // background: red;
  }

  .ant-tabs-nav-operations {
    display: none !important;
  }
}
