@import './var.scss';

.currency_label {
  .icon {
    margin-right: 0.6em;
  }
}

.token-logo {
  display: inline-block;
  height: 1.2em;
  width: 1.2em;
  vertical-align: middle;
  margin-right: 4px;
  margin-left: 0;
  // border: 1px solid #414141;
  border-radius: 50%;
}

.purple-text {
  color: #5915e1;
  display: block;
}

.cur-label {
  margin-top: 10px;
  margin-bottom: 3px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  .icon {
    display: inline-flex;
    align-items: center;
  }
}

.cur-reserve-container {
  background: #fff;
  border-radius: 15px;
  margin: 30px auto;
  padding: 30px 24px;
  max-width: min(728px, 90vw);

  @include mobile-only {
    width: calc(100% - 30px);
    margin: 15px auto;
    padding: 20px 15px;
  }
}

.cur-reserve,
.my-share {
  display: inline-block;
  text-align: left;
  vertical-align: top;
  font-size: 12px;
  line-height: 24px;

  @include desktop-only {
    width: 50%;
  }

  @include mobile-only {
    width: 100%;
  }

  .title-text {
    font-size: 18px;
    line-height: 16px;
    font-weight: bold;
    margin-bottom: 30px;

    @include mobile-only {
      margin-bottom: 20px;
      text-align: center;
    }
  }

  ul li {
    line-height: 22px;
  }
}

.cur-reserve {
  @include desktop-only {
    flex-grow: 1;
  }

  &:not(:last-child) {
    @include mobile-only {
      margin-bottom: 30px;
    }
  }
}

.token-max {
  display: inline-block;
  margin-left: 15px;
  cursor: pointer;
  font-size: 12px;
}

// .token-max:hover {
//   text-decoration: underline;
// }

.error-tip {
  text-align: center;
  margin: 8px 0;
  color: red;
}

.error-content {
  min-height: 40px;
  // padding: 0 10px;
}

.add-liq {
  legend {
    margin-bottom: 10px !important;
  }

  fieldset {
    // margin-top: 10px !important;
  }

  .deposit-check {
    padding: 0 10px 0 0;

    .ant-checkbox-wrapper {
      font-size: 14px;
      line-height: 25px;
    }
  }

  .percentage {
    // margin-top: 20px !important;
  }

  .currencies {
    // margin-bottom: 25px !important;
  }

  ul li {
    padding-bottom: 10px;
  }

  // .cur-input {
  //   margin-top: 7px;
  //   input {
  //     opacity: 0.3 !important;
  //     border: 1px solid #979797 !important;

  //     &:hover, &:focus {
  //       opacity: 1 !important;
  //       border: 1px solid #5915E1 !important;
  //     }
  //   }
  // }
}

.action-btn {
  margin-top: 30px;
  margin-bottom: 20px;

  button {
    line-height: 18px;
  }
}

.withdraw-liq {
  legend {
    text-align: left;
  }

  .withdraw-one {
    overflow: hidden;
    margin-top: 10px;

    .token-logo {
      transform: translateY(-1px);
    }

    >div {
      float: left;
      margin-right: 30px;
    }
  }
}

.incl-price {
  padding: 10px;
  text-align: center;
  border-radius: 5px;
  font-size: 12px;
}

.gray-color {
  background: #ececec;
  color: #000;
}

.blue-color {
  color: #fff;
  background: #55d1b3;
}

