//     margin-bottom: 70px;
//   }
// }

.swap-detail {
  // background: #4180db;
  // color: #fff;
  padding: 10px;
  color: #000;
  background: #ececec;
  border-radius: 5px;

  .slippage-tip {
    color: #999;
  }
}

.high-slippage {
  color: #fff;
  background: #d15955;
  font-size: 12px;
  border-radius: 5px;
}

.common-err-bg {
  background: #d15955;
  color: #fff;
}

.pad10 {
  padding: 10px;
}

.slippage-modal {
  .common-err-bg {
    padding: 10px 10px;
    text-align: left;
    color: #fff;
  }

  .ant-modal-title {
    text-align: center;
  }

  .ant-modal-body {
    padding-top: 0;
  }

  .ant-checkbox-wrapper {
    margin: 10px 0;

    >span:last-child {
      color: #e60c0e;
    }
  }

  .btns {
    justify-content: center;

    >button:first-child {
      margin-right: 10px;
    }
  }
}

.max-slippage {
  margin-left: 0px;
  margin-top: 5px;
}

.withdraw-one {
  // padding: 0 10px;
}

.pc-adjust {
  margin-top: -2px;
  margin-right: 10px;
}

.switch3sun {
  margin: 10px 0;

  .ant-checkbox-wrapper {
    span:last-child {
      padding-left: 13px;
    }
  }
}

@media screen and (max-width: 500px) {
  .action-btn {
    margin-top: 10px;
    margin-bottom: 16px;
  }

  .add-liq,
  .withdraw-liq {
    .anticon {
      margin-top: 2px;
    }
  }

  .withdraw-one {
    div:nth-child(3) {
      margin-top: 10px;
    }

    div:nth-child(4) {
      margin-top: 10px;
      margin-left: 2px;
    }
  }

  .mobile-block {
    div:nth-child(1) {
      margin-top: 10px;
    }

    .ad-radios .ad-radio {
      margin-top: -5px;
    }
  }

  #root .ad-radio .ant-input {
    height: 26px;
  }
}