@import './var.scss';

.wrapper {
  // width: 750px;
  // margin: 2rem auto;

  .ant-select-selector {
    width: 200px;
  }

  .buy-sell {
    position: relative;
    cursor: default;
    color: #333;
    padding: 2em;

    .input {
      background: #4c4c4c;
      color: #fff;
    }

    .fieldset {
      display: flex;
      justify-content: space-between;
      align-items: center;

      // &.fieldset-slippage {
      //   padding: 6px 12px;
      // }

      > fieldset {
        width: 50%;
        max-height: 230px;
        border: 1px solid #efefef;
        padding: 0 10px 10px !important;
        margin-bottom: 10px;
        position: relative;

        > legend {
          font-size: 13px !important;
          color: #333 !important;
        }

        &.advanced {
          width: 100%;
          padding: 1rem 0 !important;
          border: none;

          .advanced-options {
            display: flex;
            align-items: center;
            @include mobile-only {
              flex-direction: column;
              align-items: flex-start;

              .ad-radios {
                display: flex;
                flex-direction: column;
              }
            }
            .input {
              background: #ffffff;
              border: 1px solid #e1e1e1;
              box-sizing: border-box;
              border-radius: 6px;
            }
          }
        }

        > input {
          color: #333 !important;
          background-color: #fff !important;
        }
      }

      > img {
        width: 30px;
        height: 30px;
        margin: 0 10px;
        cursor: pointer;
      }

      .st-input {
        width: 100%;
        height: 24px;
        line-height: 24px;
        padding: 1px 0 1px 0;
        border-radius: 0;
        background: #4c4c4c;
      }

      .exchange-alt {
        transform: rotate(90deg);
      }

      .exchange-alt-new {
        transform: rotate(0deg);
      }
    }

    .exchange-img {
      background: #ececef;
      border-radius: 4px;
      width: 22px;
      // height: 12px;
      margin: 1rem;
      padding: 4px;
      cursor: pointer;
    }

    .exchange-rate {
      display: flex;
      align-items: center;
      justify-content: space-between;
      color: #999;
      @include mobile-only {
        justify-content: start;
      }
      // margin-top: 14px;
      // padding: 0 12px;

      // &.pool3 {
      //   justify-content: flex-end;
      // }
    }

    h4 {
      color: #fff;
    }
  }

  .red-error {
    margin-top: 10px;
  }

  .buy-sell {
    .red-error {
      margin-top: 0;
    }
  }
}

.token-logo {
  display: inline-block;
  height: 1.4em !important;
  width: 1.4em !important;
  vertical-align: middle;
  margin-right: 4px;
  margin-left: 0;
  // border: 1px solid #414141;
  border-radius: 50%;
}

.sell-submit-wrapper {
  display: flex;
  justify-content: center;
  margin: 22px auto 0;

  &.with-bottom-margin {
    margin-bottom: 30px;
  }

  .tab-btn {
    border-radius: 10px !important;
    // padding: 10px 130px;
    margin: 0 auto;
    width: 300px;
    height: 40px !important;
    font-weight: 400;
    font-size: 14px !important;
    line-height: 20px;
    display: flex;
    align-items: center;
    text-align: center;
  }
}

.sell-btn {
  display: inline-block;
  // width: 40%;
  color: #4e545a;
  font-size: 14px;
  padding: 0.25em 1.5em;
  position: relative;
  -webkit-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in;
  border: 3px solid #4e545a;
  box-sizing: border-box;
  border-radius: 4px;
  user-select: none;

  &.gray {
    color: #b7aeae;
    background: #4c4c4c;
    cursor: not-allowed;
  }
}

.advanced-header {
  // margin-top: 10px;
  // padding: 0 12px;
  border-radius: 6px;
}

.advanced-btn {
  position: relative;
  display: flex;
  line-height: 30px;
  width: fit-content;
  height: 32px;
  padding: 10px;
  background: #ded0f9;
  text-align: center;
  justify-content: center;
  align-items: center;
  color: #5915e1;
  cursor: pointer;
  border-radius: 6px;

  &.current .down-icon {
    transform: rotate(180deg) translateY(4px);
  }

  .down-icon {
    margin: 0;
    padding: 0;
    height: 14px;
    width: 14px;
    // padding-bottom: 10px;
    background-image: url(../images/purple-up-arrow.svg);
    background-repeat: no-repeat;
    transform: translateX(4px) translateY(4px);
  }

  // &::after {
  // content: '';
  // display: inline-block;
  // width: 0;
  // height: 0;
  // position: absolute;
  // right: 8px;
  // top: 11px;
  // border: 6px solid transparent;
  // border-top-color: rgba(51, 51, 51, 0.3);
  // }

  // &.current::after {
  // top: 6px;
  // border: 6px solid transparent;
  // border-bottom-color: rgba(51, 51, 51, 0.3);
  // }
}

.max {
  text-align: right;
  cursor: pointer;
  margin-top: 15px;

  > span {
    display: inline-block;
    height: 30px;
    line-height: 30px;
    color: #7c8ead;
    position: relative;
    top: -6px;

    &:hover {
      text-decoration: underline;
    }
  }
}

.slippage {
  font-size: 14px;
  color: #333;
}

.error-msg {
  height: 20px;
  text-align: center;
  color: #f00;
}

.h-30 {
  height: 30px;
}

@media screen and (max-width: 800px) {
  #root .wrapper {
    width: calc(100% - 30px);

    .buy-sell {
      .fieldset {
        flex-direction: column;
        align-items: flex-start;

        > fieldset {
          width: 100%;
          padding: 10px !important;

          &.advanced {
            padding: 10px 10px 0px 0 !important;
          }
        }

        > img {
          // transform: rotate(90deg);
          margin: 6px 0;
        }

        .exchange-alt {
          transform: rotate(0deg);
          margin: 5px auto;
        }

        .exchange-alt-new {
          transform: rotate(90deg);
        }
      }

      .exchange-rate {
        display: block;
        font-size: 12px;

        > div:first-child {
          margin-bottom: 5px;
        }
      }
    }

    .window:last-child {
      margin-bottom: 0;
    }

    // .sell-submit {
    //   margin-top: 0;
    // }
  }

  .max {
    margin-top: 5px;
  }
}

@media screen and (max-width: 500px) {
  .wrapper {
    .buy-sell .fieldset {
      flex-direction: column;

      > fieldset {
        width: 100%;
      }

      > img {
        margin: 6px 0;
      }
    }
  }

  .ad-radios {
    display: flex;
    flex-direction: column;
  }
}

@media screen and (max-width: 400px) {
  .er-en {
    width: 25%;
  }
}

.pc-input-box {
  background: #ffffff;
  border: 1px solid #efefef;
  box-sizing: border-box;
  border-radius: 15px;
  padding: 1rem;
  width: 47%;

  .text-part {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
    .from-to-text {
      font-weight: 600;
      font-size: 18px;
      line-height: 16px;
      display: flex;
      align-items: center;
      text-align: center;
      color: #333333;
    }
  }
}

.flex-column-container {
  display: flex;
  flex-direction: column;

  & > * {
    &:not(:last-child) {
      margin-bottom: 10px !important;
    }
  }
  .ant-select {
    position: relative;
    width: 100px !important;

    .ant-select-arrow {
      margin-left: 0;
    }

    .anticon {
      margin: 0;
    }
  }
}

.connect-wallet-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
  position: relative;
  top: -24px;

  .wallet-image {
    @include mobile-only {
      width: 80px;
      height: 80px;
    }
  }
}
